@use 'global' as *;
.room__title {
    position: relative;
    font-size: rem(36);
    font-family: $yumincho;
    font-weight: 500;
    line-height: 1.7;
    text-align: center;
}

.room__title:before {
    display: inline-block;
    position: absolute;
    content: '';
    bottom: rem(-17);
    left: 50%;
    width: rem(52);
    height: 1px;
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translate(-50%);
    background-color: $white;
}

.room__text-area {
    text-align: center;
    margin-top: rem(55);
    font-size: rem(16);
    font-family: $yugothic;
    font-weight: 500;
    line-height: 1.75;
}

.room__text2 {
    margin-top: rem(33);
}
